    // },
    addClass(id) {
      this.activeId = id;
      console.log('导航栏中当前高亮的部分', this.activeId);
      if (this.activeId != "blog")
        this.$router.push({ path: '/' + id })
      else
        this.$router.push({ path: '/' })
    },
    changeTheme() {
      this.theme++;
      if (this.theme >= 2) this.theme = 0;
      switch (this.theme) {
        case 0:
          document.documentElement.setAttribute("data-theme", "theme");
          break;
        case 1:
          document.documentElement.setAttribute("data-theme", "theme1");
          break;
      }
    },
  },
  mounted() {
    // 解决：Vue页面手动刷新，导航栏激活项还原到初始状态问题  
    console.log('当前路径', this.$route.path);
    var str = this.$route.path
    var str1 = str.substring(1)
    console.log('去除/', str1);
    this.activeId = str1

    // 点击编辑文章中的  编辑,管理留言   , 导航栏会有一点问题
  }



};
